import { Injectable } from '@angular/core';
import { splitDepsDsl } from '@angular/core/src/view/util';

@Injectable({
  providedIn: 'root'
})
export class GithubService {
  static readonly regex = /github\.com\/([a-zA-Z_]*)\/([a-zA-Z_]*)/;

  constructor() { }

  isValidGithubUrl(url: string) {
    return GithubService.regex.test(url);
  }
  parseGithubUrl(url: string) {
    let r = GithubService.regex.exec(url);
    if (r == null) return;
    return {
      owner: r[1], repo: r[2]
    }
  }
}
