import { Component } from '@angular/core';
import { CbbApiService } from './cbb-api.service';
import { GithubService } from './github.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent {
  title = 'cbb';

  repoUrl: string = "";
  isValidRepoUrl = false;
  isLoading = false;

  constructor(
    private api: CbbApiService,
    private github: GithubService) {

  }

  onChangeGithubUrl() {
    this.isValidRepoUrl = this.github.isValidGithubUrl(this.repoUrl);
    console.log(this.isValidRepoUrl);
  }
  async onClickAnalyze() {
    if (this.isValidRepoUrl == false)
      return;

    let repo = this.github.parseGithubUrl(this.repoUrl);

    this.isLoading = true;
    await this.api.requestAnalyze(repo.owner, repo.repo);
    this.isLoading = false;
  }
}
